/* --- Основной стиль руки игрока с картами --- */
.player-hand {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  gap: 28px;
  width: 80vw; /* Используем vw для динамического масштабирования по ширине экрана */
  max-width: 1200px; /* Ограничение максимальной ширины */
  overflow: visible;
  pointer-events: auto;
}

.card-in-hand-wrapper {
  position: relative;
  max-width: calc((100% - 8px * 4) / 5);
  min-width: 48px;
  width: 18vw; /* Карта занимает ~16% ширины экрана — регулируйте */
  max-width: 140px; /* максимальный размер карты */
  transition: transform 0.15s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  border-radius: 4px; /* 👈 Совпадает с .card-in-hand-wrapper */
}

.card-in-hand-wrapper:hover {
  transform: translateY(-20px) scale(1.1) rotateZ(8deg);
  z-index: 3;
  box-shadow: 0 0 20px rgba(255, 165, 0, 0.7);
  transition: transform 0.1s cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 0.2s ease-in-out;
}

.card-in-hand-image {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  object-fit: contain;
  user-select: none;
  pointer-events: none;
  background-color: #1e1e1e; /* Темный фон */
  box-shadow: 0 0 12px rgba(255, 165, 0, 0.4);
  transition: box-shadow 0.3s ease-in-out;
}

/* Угловые блоки с ценой и уроном */
.card-corner {
  position: absolute;
  font-weight: bold;
  font-size: 1em;
  padding: 0.2em 0.5em;
  border-radius: 6px;
  user-select: none;
  pointer-events: none;
  text-shadow: 2px 2px 3px #000;
  color: #fff;
  white-space: nowrap;
  z-index: 5;
}

.card-corner.cost {
  color: #00ff00;
  top: -12px;
  left: -12px;
}

.card-corner.damage,
.card-corner.damage_over_time {
  color: #ff4444 !important; /* красный */
}

.card-corner.damage_multiplier {
  color: #cc66ff !important; /* фиолетовый */
}

.card-corner.top-left {
  top: 6px;
  left: 6px;
}
.card-corner.top-right {
  top: 6px;
  right: 6px;
}
.card-corner.bottom-left {
  bottom: 6px;
  left: 6px;
}

/* Кнопка «Играть карту» */
.playcardbutton {
  position: absolute;
  bottom: calc(100% + 8px);
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(145deg, #3a3a3a, #2a2a2a);
  color: #f5f5f5;
  font-weight: bold;
  font-size: 14px;
  padding: 6px 14px;
  border-radius: 6px;
  border: 1px solid rgba(255, 165, 0, 0.2);
  z-index: 2000;
  display: block;
  opacity: 1;
  animation: popIn 0.15s ease-out;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.45),
    inset 0 1px 0 rgba(255, 255, 255, 0.08),
    inset 0 -2px 0 rgba(0, 0, 0, 0.45);
}
.playcardbutton:hover {
  background: linear-gradient(145deg, #444, #2f2f2f);
  transform: translateX(-50%) scale(1.08);
  box-shadow: 0 12px 22px rgba(0, 0, 0, 0.55),
    0 0 0 1px rgba(255, 165, 0, 0.2);
}

@keyframes popIn {
  0% {
    opacity: 0;
    transform: translateX(-50%) scale(0.85);
  }
  100% {
    opacity: 1;
    transform: translateX(-50%) scale(1);
  }
}

/* Выделенная карта */
.card-in-hand-wrapper.selected {
  z-index: 5;
  transform: translateY(-14px) scale(1.15) rotateZ(3deg);
  box-shadow: 0 0 18px 5px #ffa500cc;
  transition: transform 0.1s ease, box-shadow 0.2s ease;
}

/* --- Новая динамичная анимация атаки босса --- */
.playing-card-fly {
  position: fixed;
  width: 140px; /* чуть больше для эффекта */
  height: auto;
  border-radius: 12px;
  object-fit: contain;
  pointer-events: none;
  user-select: none;
  animation: boss-attack-dynamic 0.35s linear forwards;
  z-index: 35;
  bottom: 120px;
  left: 50%;
  transform: translateX(-50%);
  will-change: transform, opacity, filter;
  filter: drop-shadow(0 0 12px #ffa500);
}

/* Энергетическое свечение с мерцанием и расширением */
.playing-card-fly::before,
.playing-card-fly::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  pointer-events: none;
  opacity: 0.7;
  animation-timing-function: ease-in-out;
  will-change: transform, opacity, box-shadow;
  animation-iteration-count: infinite;
}

.playing-card-fly::before {
  width: 160%;
  height: 160%;
  top: -30%;
  left: -30%;
  box-shadow: 0 0 40px 18px rgba(255, 165, 0, 0.7),
    0 0 60px 30px rgba(255, 200, 0, 0.5),
    inset 0 0 40px 20px rgba(255, 165, 0, 0.85);
  animation: pulse-glow-dynamic 0.7s ease-in-out infinite alternate;
}

.playing-card-fly::after {
  width: 250%;
  height: 250%;
  top: -75%;
  left: -75%;
  box-shadow: 0 0 50px 30px rgba(255, 165, 0, 0.4),
    0 0 90px 50px rgba(255, 220, 0, 0.25);
  animation: pulse-glow-dynamic 1.3s ease-in-out infinite alternate;
}

/* Усиленный пульс свечения */
@keyframes pulse-glow-dynamic {
  0% {
    transform: scale(0.9);
    opacity: 0.3;
    box-shadow: 0 0 40px 15px rgba(255, 165, 0, 0.6),
      0 0 60px 25px rgba(255, 200, 0, 0.45);
  }
  50% {
    transform: scale(1.15);
    opacity: 0.8;
    box-shadow: 0 0 60px 30px rgba(255, 165, 0, 1),
      0 0 80px 40px rgba(255, 230, 0, 0.6);
  }
  100% {
    transform: scale(0.95);
    opacity: 0.4;
    box-shadow: 0 0 50px 20px rgba(255, 165, 0, 0.8),
      0 0 70px 30px rgba(255, 220, 0, 0.5);
  }
}

@keyframes boss-attack-dynamic {
  0% {
    opacity: 1;
    transform: translateX(-50%) translateY(0) scale(1) rotate(0deg);
    filter: brightness(1) drop-shadow(0 0 12px #ffa500);
  }
  30% {
    /* Плавное медленное движение по Y */
    transform: translateX(-50%) translateY(-120px) scale(1.1) rotate(0deg);
    filter: brightness(1.3) drop-shadow(0 0 25px #ffcc00);
    opacity: 1;
  }
  60% {
    /* Ускоренное движение дальше вниз */
    transform: translateX(-50%) translateY(-350px) scale(1.5) rotate(0deg);
    filter: brightness(1.7) drop-shadow(0 0 45px #ff8800);
    opacity: 0.85;
  }
  85% {
    /* Ещё быстрее и масштаб уменьшается */
    transform: translateX(-50%) translateY(-480px) scale(1.3) rotate(0deg);
    filter: brightness(1.9) drop-shadow(0 0 55px #ff6600);
    opacity: 0.5;
  }
  100% {
    /* Конец — исчезновение */
    opacity: 0;
    transform: translateX(-50%) translateY(-550px) scale(0.2) rotate(0deg);
    filter: brightness(2.5) blur(3px);
  }
}

/* --- Тряска экрана для визуализации попадания по боссу --- */
.screen-shake {
  animation: shake 0.5s linear;
}

@keyframes shake {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }
  10% {
    transform: translate(-5px, 5px) rotate(-1deg);
  }
  20% {
    transform: translate(5px, -5px) rotate(1deg);
  }
  30% {
    transform: translate(-5px, -5px) rotate(-1deg);
  }
  40% {
    transform: translate(5px, 5px) rotate(1deg);
  }
  50% {
    transform: translate(-3px, 3px) rotate(0deg);
  }
  60% {
    transform: translate(3px, -3px) rotate(-1deg);
  }
  70% {
    transform: translate(-3px, -3px) rotate(1deg);
  }
  80% {
    transform: translate(3px, 3px) rotate(0deg);
  }
  90% {
    transform: translate(-2px, 2px) rotate(-1deg);
  }
  100% {
    transform: translate(0, 0) rotate(0deg);
  }
}

/* --- Дезактивация руки игрока --- */
.player-hand[style*="pointer-events: none"] {
  cursor: default;
  opacity: 0.7;
  user-select: none;
}

.player-bottom-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  z-index: 1000;
  background: linear-gradient(
    to top,
    rgba(30, 30, 30, 0.95),
    transparent 100px
  );
  transform: translateY(0);
  animation: bar-slide-in 0.5s cubic-bezier(0.25, 1.25, 0.5, 1) forwards;
  will-change: transform, opacity, filter;
}

.player-bottom-bar.hidden {
  animation: bar-slide-out 0.4s cubic-bezier(0.6, -0.3, 0.7, 1.2) forwards;
}

@keyframes bar-slide-in {
  0% {
    transform: translateY(200px) scale(0.95);
    opacity: 0;
    filter: brightness(0.7) blur(3px);
  }
  60% {
    transform: translateY(-12px) scale(1.02);
    opacity: 1;
    filter: brightness(1.2);
  }
  80% {
    transform: translateY(6px) scale(0.99);
  }
  100% {
    transform: translateY(0) scale(1);
    opacity: 1;
    filter: brightness(1);
  }
}

@keyframes bar-slide-out {
  0% {
    transform: translateY(0) scale(1);
    opacity: 1;
    filter: brightness(1);
  }
  30% {
    transform: translateY(20px) scale(0.98);
    opacity: 0.9;
    filter: brightness(0.9);
  }
  100% {
    transform: translateY(200px) scale(0.95);
    opacity: 0;
    filter: brightness(0.7) blur(3px);
  }
}

.player-bottom-bar::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6), transparent);
  pointer-events: none;
  z-index: 1;
}

.player-hand-platform {
  position: relative;
  max-width: 900px;
  margin-bottom: 0px;
  background: linear-gradient(145deg, #2e2e2e, #1a1a1a);
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  padding: 24px 28px 40px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 12px;
  z-index: 2;

  /* pointer-events: none;  <-- Удалите эту строку */
  user-select: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;

  box-shadow: 0 -4px 10px rgba(0, 0, 0, 0.5),
    inset 0 8px 16px rgba(255, 165, 0, 0.05),
    inset 0 -6px 12px rgba(0, 0, 0, 0.2);
  overflow: visible;
}

/* Явно сохраняем тень при фокусе и активном состоянии */
.player-hand-platform:focus,
.player-hand-platform:active {
  box-shadow: 0 -4px 10px rgba(0, 0, 0, 0.5),
    inset 0 8px 16px rgba(255, 165, 0, 0.05),
    inset 0 -6px 12px rgba(0, 0, 0, 0.2);
  outline: none;
}

.player-hand-platform::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 10%;
  right: 10%;
  height: 8px;
  background: radial-gradient(ellipse at center, #ffa50033, transparent);
  opacity: 0.7;
  z-index: 0;
  box-shadow: 0 -4px 10px rgba(0, 0, 0, 0.5),
    inset 0 8px 16px rgba(255, 165, 0, 0.05),
    inset 0 -6px 12px rgba(0, 0, 0, 0.2);
}

.energy-indicator {
  position: absolute;
  top: -12px;
  left: -18px;
  font-size: 1.8rem;
  font-weight: bold;
  color: #00ff00; /* ярко-зелёный */
  text-shadow: 1px 1px 2px #000;
  padding: 4px 10px;
  border-radius: 6px;
  pointer-events: none;
  user-select: none;
}
:root {
  --card-width: 90px; /* 👈 возьми эти размеры из .opponent-card-back */
  --card-height: 140px;
}

/* карты на поле */
.played-card-wrapper {
  position: relative;
  width: var(--card-width);
  height: var(--card-height);
  margin: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* чтобы картинка внутри не ломала пропорции */
.played-card-wrapper .card-frame-wrapper,
.played-card-wrapper .opponent-card-back {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
