.avatar-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 8px;
  position: relative;
}

.player-avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%; /* 👈 круг */
  object-fit: cover;
  border: 2px solid #ffa500; /* оранжевая рамка */
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);

  position: relative; /* 👈 теперь damage-number будет отталкиваться от аватарки */
}

.multiplier-label {
  position: absolute;
  bottom: -18px;
  right: -6px;
  color: #a855f7;
  font-weight: 600;
  font-size: 0.9em;
  text-shadow: 0 0 6px rgba(168, 85, 247, 0.5);
  pointer-events: none;
}

.priority-label {
  font-weight: bold;
  font-size: 1.1em;
  color: #ffa500;
  margin-left: 6px; /* 👈 добавляем отступ справа от HP */
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.hp-bar-container {
  display: flex; /* горизонтальное расположение */
  align-items: center; /* выравнивание по центру по вертикали */
  gap: 6px; /* расстояние между HP и "П" */
  position: relative;
  width: auto; /* чтобы буква не накладывалась */
}

.hp-multiplier-label {
  position: absolute;
  top: -26px;
  left: 0;
  padding: 2px 8px;
  border-radius: 10px;
  background: rgba(130, 77, 255, 0.9);
  color: #fff;
  font-weight: 700;
  font-size: 0.95em;
  letter-spacing: 0.5px;
  text-shadow: 0 0 8px rgba(88, 40, 175, 0.8);
  box-shadow: 0 0 12px rgba(130, 77, 255, 0.6);
  pointer-events: none;
}

.avatar-multiplier-label {
  margin-top: 6px;
  padding: 2px 8px;
  border-radius: 10px;
  background: rgba(130, 77, 255, 0.9);
  color: #fff;
  font-weight: 700;
  font-size: 0.95em;
  letter-spacing: 0.5px;
  text-shadow: 0 0 8px rgba(88, 40, 175, 0.8);
  box-shadow: 0 0 12px rgba(130, 77, 255, 0.6);
  pointer-events: none;
  text-align: center;
}

.avatar-multiplier-top {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

/* Общие стили тултипа */
.priority-tooltip {
  position: absolute;
  top: 50%;
  left: 100%; /* справа от буквы */
  transform: translateY(-50%) translateX(8px); /* вертикально по центру, небольшой отступ справа */
  background: rgba(46, 46, 46, 0.95);
  color: #fff;
  font-size: 0.8em;
  padding: 8px 12px;
  border-radius: 8px;
  width: 220px;
  text-align: left;
  animation: fadeIn 0.2s ease-out;
  line-height: 1.4em;
  z-index: 20000;
}

/* Убираем горизонтальные правки */
.tooltip-above,
.tooltip-below {
  top: 50%; /* только вертикальное центрирование */
  left: 100%;
  transform: translateY(-50%) translateX(8px);
  z-index: 20000;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-50%) translateX(5px);
  }
  to {
    opacity: 1;
    transform: translateY(-50%) translateX(8px);
  }
}
