.game-container {
  position: relative;

  height: 100vh;
  background: linear-gradient(to top, #1a1a1a, #2e2e2e);
  color: white;
  font-family: sans-serif;
  box-sizing: border-box;
  overflow: hidden;
}

/* Левый верх/низ — общая линия ник + HP */
.info-row {
  position: absolute;
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 10px 16px;
  box-sizing: border-box;
}

.info-row.top {
  top: 0;
  justify-content: flex-start;
}

.info-row.bottom {
  bottom: 0;
  justify-content: flex-start;
}

.nickname {
  font-size: 16px;
  white-space: nowrap;
  color: #fff;
}

.hp-bar {
  width: 35vw; /* фиксированная ширина */
  height: 20px; /* высота вертикального бара */
  background-color: #444;
  border: 1px solid #777;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column-reverse; /* чтобы HP уменьшался сверху вниз */
  position: relative;
}

.hp-fill {
  width: 100%;
  height: 100%; /* управляется через inline-style height в JSX */
  background-color: #e53935;
  transition: height 0.4s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 12px;
}

.hp-text {
  z-index: 1;
  transform: rotate(180deg); /* чтобы числа шли "правильно" */
  font-size: 12px;
  text-align: center;
  color: white;
}
.game-card {
  position: relative; /* для абсолютного позиционирования кнопки */
  background-color: #333;
  border: 1px solid #fff;
  padding: 8px;
  border-radius: 8px;
  width: 90px;
  text-align: center;
  cursor: pointer;
}

.game-card-img {
  width: 80px;
  height: auto;
  margin-bottom: 5px;
}

.info-container {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none; /* Чтобы не мешало кликам */
}

.info-container.top {
  top: -20px;
}

.info-container.bottom {
  bottom: -20px;
}

.hp-bar-container {
  position: relative;
  align-items: center;
  gap: 4px;
  position: absolute;
  left: 20px; /* можно подправить */
  z-index: 22000;
}

.nickname.center {
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  position: relative;
  text-align: center;
}

.hp-text {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 13px;
}

.recipes-container {
  position: fixed; /* фиксируем относительно экрана */
  right: 20px;
  bottom: 18vh;
  background-color: #00ff00;
  color: #1a1a1a;
  font-weight: bold;
  font-size: 16px;
  padding: 8px 12px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  box-shadow: 0 0 8px #00ff0099;
  user-select: none;
  cursor: default;
  z-index: 1000; /* чтобы быть поверх */
}

.end-turn-button {
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 10;

  width: 140px; /* меньше */
  height: 34px; /* чуть меньше */
  background: linear-gradient(145deg, #3a3a3a, #2a2a2a);
  border: 1px solid rgba(255, 165, 0, 0.2);
  color: #f5f5f5;
  font-weight: 700;
  font-size: 12px; /* уменьшенный шрифт */
  text-align: center;
  text-transform: uppercase;

  border-radius: 8px;
  cursor: pointer;

  display: flex;
  align-items: center;
  justify-content: center;

  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.45),
    inset 0 1px 0 rgba(255, 255, 255, 0.08),
    inset 0 -2px 0 rgba(0, 0, 0, 0.45);
  transition: all 0.2s ease;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.end-turn-button:hover {
  background: linear-gradient(145deg, #444, #2f2f2f);
  transform: scale(1.08);
  box-shadow: 0 14px 24px rgba(0, 0, 0, 0.55),
    0 0 0 1px rgba(255, 165, 0, 0.2);
}

.end-turn-button:active {
  transform: scale(0.95);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.35);
}

/* цифра таймера без отдельной анимации */
.timer-digit {
  display: inline-block;
  transition: color 0.2s;
}

/* пульсация кнопки при таймере 10–40 (легкая) */
.end-turn-button.pulse {
  animation: pulseGlow 1s infinite;
}

/* более сильная пульсация кнопки при критическом таймере 9–0 */
.end-turn-button.pulse-strong {
  animation: pulseGlowStrong 0.8s infinite;
}

@keyframes pulseGlow {
  0% {
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.12), 0 0 0 transparent;
    transform: scale(1);
  }
  50% {
    box-shadow: 0 0 18px rgba(255, 255, 255, 0.18),
      0 0 6px rgba(255, 255, 255, 0.1);
    transform: scale(1.04);
  }
  100% {
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.12), 0 0 0 transparent;
    transform: scale(1);
  }
}

@keyframes pulseGlowStrong {
  0% {
    box-shadow: 0 0 14px rgba(255, 255, 255, 0.18), 0 0 0 transparent;
    transform: scale(1);
  }
  50% {
    box-shadow: 0 0 28px rgba(255, 255, 255, 0.28),
      0 0 12px rgba(255, 255, 255, 0.16);
    transform: scale(1.08);
  }
  100% {
    box-shadow: 0 0 14px rgba(255, 255, 255, 0.18), 0 0 0 transparent;
    transform: scale(1);
  }
}

.played-cards-section {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  gap: 16px;
}

.played-card {
  width: 120px;
  height: 180px;
  border: 2px solid #ffa500;
  border-radius: 8px;
  background-color: #2e2e2e;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px;
  color: white;
  box-shadow: 0 0 10px #ffa500;
}
.undo-card-button {
  position: absolute; /* кнопка поверх */
  bottom: -28px; /* смещаем ниже карты */
  left: 50%;
  transform: translateX(-50%);
  margin-top: 0; /* убираем старый отступ */
  padding: 4px 10px;
  font-size: 12px;
  cursor: pointer;
  background-color: #ff4d4d;
  border: none;
  border-radius: 4px;
  color: white;
  transition: background-color 0.2s ease;
  z-index: 10; /* чтобы быть поверх карты */
}

.undo-card-button:hover {
  background-color: #cc0000;
}

.played-card-img {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 6px;
}
.playcardbutton:disabled {
  background-color: #555;
  cursor: not-allowed;
  opacity: 0.6;
}
.xp-row {
  margin-top: 4px;
  text-align: center;
  font-size: 12px;
  opacity: 0.85;
}

.waiting-message {
  color: greenyellow; /* оранжевый текст */
  border: 2px solid rgba(173, 255, 47, 0.5); /* полупрозрачная рамка */
  padding: 10px 20px;
  border-radius: 12px;
  background: rgba(0, 0, 0, 0.8); /* слегка затемнённый фон */
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);

  /* 👇 позволяет регулировать расположение */
  position: absolute;
  top: 15%; /* можно менять */
  left: 50%; /* центрируем по горизонтали */
  transform: translateX(-50%);
  z-index: 1000;
}
.opponent-hand {
  display: flex;
  justify-content: center;
  margin: 16px 0;
}

.opponent-card-back {
  width: 80px;
  height: 120px;
  margin: 0 4px;
  border-radius: 8px;
  border: 2px solid #ffa500;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.6);
  background-color: #222;
  object-fit: cover;
}
.round-indicator {
  position: fixed;
  top: 50%;
  left: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffa500;
  font-weight: bold;
  padding: 6px 12px;
  border-radius: 12px;
  font-size: 2.2em;
  pointer-events: none;
  z-index: 1000;
  text-align: center;
}

/* красные края экрана */
.damage-flash {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 0, 0, 0.3);
  pointer-events: none;
  z-index: 9999;
  animation: flashRed 0.6s ease-out;
}
@keyframes flashRed {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
/* цифра урона */
.damage-number {
  position: absolute;
  left: 50%;
  font-size: 1.8em;
  font-weight: bold;
  color: #ff3b3b;
  text-shadow: 0 0 6px rgba(0, 0, 0, 0.7);
  pointer-events: none;
  z-index: 3000;
}

.damage-number-up {
  bottom: 100%; /* 👈 над аватаркой */
  transform: translateX(-50%);
  animation: floatUp 0.8s ease-out forwards;
}

.damage-number-down {
  top: 100%; /* 👈 под аватаркой */
  transform: translateX(-50%);
  animation: floatDown 0.8s ease-out forwards;
}

@keyframes floatUp {
  0% {
    transform: translate(-50%, 0);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -40px);
    opacity: 0;
  }
}

@keyframes floatDown {
  0% {
    transform: translate(-50%, 0);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, 40px);
    opacity: 0;
  }
}
.damage-number {
  text-shadow: 0 0 6px black;
  z-index: 3000;
}
