/* ✦ Карточка летит */
@keyframes card-strike-flight {
  0% {
    transform: translate(0px, 0px) scale(1) rotate(0deg);
    filter: brightness(1);
  }
  60% {
    transform: translate(calc(var(--dx, 0px) * 0.9), calc(var(--dy, 0px) * 0.9))
      scale(1.1) rotate(var(--rot, 6deg));
    filter: brightness(1.3) contrast(1.2);
  }
  100% {
    transform: translate(var(--dx, 0px), var(--dy, 0px)) scale(1)
      rotate(var(--rot, 8deg));
    filter: brightness(1);
  }
}
/* ✦ Хлёсткий удар с предварительным наклоном к аватару */
@keyframes card-break {
  0% {
    transform: translate(0, 0) scale(1) rotate(0deg);
    opacity: 1;
  }
  15% {
    /* быстрый наклон в сторону аватара */
    transform: translate(0, 0) scale(1.05) rotate(var(--tilt, 0deg));
    opacity: 1;
  }
  25% {
    /* вернулась в "боевую стойку" */
    transform: translate(0, 0) scale(1.1) rotate(0deg);
    opacity: 1;
  }
  70% {
    transform: translate(
        calc(var(--dx, 0px) * 0.95),
        calc(var(--dy, 0px) * 0.95)
      )
      scale(1.1) rotate(0deg);
    opacity: 1;
  }
  80% {
    transform: translate(var(--dx, 0px), var(--dy, 0px)) scale(1) rotate(0deg);
    opacity: 1;
  }
  90% {
    transform: translate(calc(var(--dx, 0px) * 0.9), calc(var(--dy, 0px) * 0.9))
      scale(0.95) rotate(-10deg);
    opacity: 0.8;
  }
  100% {
    transform: translate(var(--dx, 0px), var(--dy, 0px)) scale(0.8)
      rotate(30deg);
    opacity: 0;
  }
}

.card-strike {
  position: relative;
  z-index: 99900;
  animation: card-break 500ms cubic-bezier(0.6, -0.3, 0.7, 1.2) forwards;
  will-change: transform, opacity;
}
/* ✦ Отдельное исчезновение */
@keyframes card-fadeout {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 0.7;
  }
  100% {
    opacity: 0;
  }
}

/* ✦ Реакция аватара — удар с тряской */
@keyframes avatar-hit-pop {
  0% {
    transform: scale(1) translate(0, 0);
    filter: brightness(1);
  }
  30% {
    transform: scale(1.12) translate(-2px, 1px);
    filter: brightness(1.25);
  }
  60% {
    transform: scale(0.96) translate(2px, -1px);
    filter: brightness(0.9);
  }
  100% {
    transform: scale(1) translate(0, 0);
    filter: brightness(1);
  }
}

/* ✦ Атака DoT: туда-обратно, без исчезновения */
@keyframes card-dot-strike {
  0% {
    transform: translate(0, 0) scale(1) rotate(0deg);
    opacity: 1;
  }
  20% {
    transform: translate(0, 0) scale(1.05) rotate(var(--tilt, 0deg));
    opacity: 1;
  }
  40% {
    transform: translate(0, 0) scale(1.1) rotate(0deg);
    opacity: 1;
  }
  70% {
    transform: translate(
        calc(var(--dx, 0px) * 0.95),
        calc(var(--dy, 0px) * 0.95)
      )
      scale(1.1) rotate(0deg);
    opacity: 1;
  }
  100% {
    /* возвращение обратно */
    transform: translate(0, 0) scale(1) rotate(0deg);
    opacity: 1;
  }
}

/* ✦ Реакция аватара — мягкий пульс лечения */
@keyframes avatar-heal-pulse {
  0% {
    transform: scale(1);
    filter: brightness(1);
  }
  40% {
    transform: scale(1.15);
    filter: brightness(1.3) drop-shadow(0 0 18px rgba(0, 255, 128, 0.6));
  }
  70% {
    transform: scale(0.95);
    filter: brightness(1.15) drop-shadow(0 0 10px rgba(0, 255, 128, 0.4));
  }
  100% {
    transform: scale(1);
    filter: brightness(1);
  }
}

.avatar-heal {
  animation: avatar-heal-pulse 500ms ease-out;
}

/* ✦ Анимация для карт лечения — летят к игроку с мягким свечением */
@keyframes card-heal-flight {
  0% {
    transform: translate(0, 0) scale(1) rotate(0deg);
    opacity: 1;
    filter: brightness(1) drop-shadow(0 0 0px rgba(0, 255, 128, 0));
  }
  40% {
    transform: translate(calc(var(--dx, 0px) * 0.6), calc(var(--dy, 0px) * 0.6))
      scale(1.15) rotate(var(--rot, -6deg));
    opacity: 1;
    filter: brightness(1.3) drop-shadow(0 0 12px rgba(0, 255, 128, 0.6));
  }
  70% {
    transform: translate(calc(var(--dx, 0px) * 0.9), calc(var(--dy, 0px) * 0.9))
      scale(1.05) rotate(var(--rot, -2deg));
    opacity: 1;
    filter: brightness(1.2) drop-shadow(0 0 18px rgba(0, 255, 128, 0.8));
  }
  100% {
    transform: translate(var(--dx, 0px), var(--dy, 0px)) scale(0.9) rotate(0deg);
    opacity: 0;
    filter: brightness(1) drop-shadow(0 0 0px rgba(0, 255, 128, 0));
  }
}

.card-heal {
  position: relative;
  z-index: 99900;
  animation: card-heal-flight 800ms cubic-bezier(0.55, -0.3, 0.6, 1.3) forwards;
  will-change: transform, opacity, filter;
}

.card-dot-strike {
  position: relative;
  z-index: 99900;
  animation: card-dot-strike 600ms cubic-bezier(0.6, -0.3, 0.7, 1.2) forwards;
  will-change: transform, opacity;
}

.avatar-hit {
  animation: avatar-hit-pop 260ms ease-out;
}

.played-card-wrapper {
  position: relative;
}
.played-card-wrapper.hidden {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-out;
}

.board-center {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: min(90vw, 1200px);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  pointer-events: none;
  z-index: 5;

  /* 👇 добавляем отступы сверху и снизу */
  padding-top: 24vh; /* обрезать верхнее поле */
  padding-bottom: 24vh; /* обрезать нижнее поле */
  box-sizing: border-box; /* чтобы padding не ломал высоту */
}

/* Каждая половина — ровно 50% */
.board-half {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: auto; /* внутри можно кликать */
}

/* Ряды карт */
.board-row {
  display: flex;
  gap: 12px;
}

.board-half.opponent {
  border-bottom: 2px dashed rgba(255, 255, 255, 0.3); /* разделитель по центру */
}

.board-half.player {
  border-top: 2px dashed rgba(255, 255, 255, 0.3);
}

/* Скрывает карту сразу после анимации, не удаляя элемент из DOM */
.card-hidden-after-anim {
  visibility: hidden !important;
  opacity: 0 !important;
  pointer-events: none !important;
  transition: none !important;
  -webkit-transition: none !important;
}

/* Дополнительно: защитить от возможных transform/animation возвратов */
.card-hidden-after-anim,
.card-hidden-after-anim * {
  transform: none !important;
  animation: none !important;
}
